import React from "react";
import classes from './Post.module.css'
import avatarSmall from "../../../../assets/images/Kenobi_from_Kenobi.png";
import likePic from "../../../../assets/images/likev1.png";

const Post = (props) => {


    return (
        
            <div className={classes.item}>

                {/*<img src="https://icons.iconarchive.com/icons/diversity-avatars/avatars/256/batman-icon.png" alt=""/>*/}
                {/*<img src="https://cdn.iconscout.com/icon/premium/png-256-thumb/stormtrooper-1-617497.png" alt=""/>*/}
                <img src={props.profile.photos.small} alt=""/>

                <b>{props.profile.fullName}</b><br/>

                <div className={classes.textMessage}>{props.message}</div>

                <div className={classes.like}><img src={likePic}/>{props.likes}</div>

            </div>

    )
}
export default Post